import PropTypes from 'prop-types';
import { Avatar, Card, Divider, Grid, Typography } from '@material-ui/core';
import { styled } from '@material-ui/core/styles';

import { fShortenNumber } from '../../../utils/formatNumber';

const AvatarContainer = styled('div')(() => ({
  display: 'flex',
  position: 'relative',
  justifyContent: 'center',
}));

// ----------------------------------------------------------------------

function InfoItem(caption, number) {
  return (
    <Grid item xs={6}>
      <Typography variant="caption" sx={{ mb: 0.5, color: 'text.secondary', display: 'block' }}>
        {caption}
      </Typography>
      <Typography variant="subtitle1">{fShortenNumber(number)}</Typography>
    </Grid>
  );
}

SocialAccountCard.propTypes = {
  accountInfo: PropTypes.object.isRequired,
};

function SocialAccountCard({ accountInfo, ...other }) {
  const { type, name, image, follower = 0, reachWeekly = 0 } = accountInfo;
  return (
    <Card {...other} sx={{ py: 10, px: 3, textAlign: 'center' }}>
      <AvatarContainer>
        <Avatar
          alt={name}
          src={image || '/static/mock-images/avatars/avatar_14.jpg'}
          sx={{
            width: 64,
            height: 64,
            zIndex: 11,
            position: 'absolute',
            transform: 'translateY(-50%)',
          }}
        />
      </AvatarContainer>
      <Typography variant="subtitle1" align="center" sx={{ mt: 6 }}>
        {type}
      </Typography>

      <Typography variant="body2" align="center" sx={{ color: 'text.secondary' }}>
        {`@${name}`}
      </Typography>

      <Divider />

      <Grid container justifyContent="center" sx={{ py: 3, textAlign: 'center' }}>
        {InfoItem('Follower', follower)}
        {InfoItem('Reach Weekly', reachWeekly)}
      </Grid>
    </Card>
  );
}

export default SocialAccountCard;
